header {
	width: 100%;
	background: #333;
	// border-bottom: 1px solid #000;

	.inner {
		width: 100%;
		height: 80px;
		margin: 0px auto;
		position: relative;
		box-shadow: 3px 3px 10px #000;

		#gnb {
			position: absolute;
			bottom: 0px;
			left: 50%;
			transform: translateX(-50%);
			li {
				float: left;
				padding-right: 10px;

				a {
					display: inline-block;
					padding: 22px 30px;
					font: 20px/2 'radio canada';
					color: #fff;
				}
			}
		}
	}
}

//m.p
@media screen and (max-width: $mp) {
	header {
		.inner {
			width: 100%;
			height: 60px;
			margin: 0px auto;
			position: relative;
			box-shadow: 3px 3px 10px #000;

			#gnb {
				position: absolute;
				bottom: 0px;

				li {
					float: left;
					padding-right: 10px;

					a {
						display: inline-block;
						padding: 2px;
						margin-bottom: 20px;
						font: 16px/1 'radio canada';
						color: #fff;
					}
				}
			}
		}
	}
}
